@use "../settings/colors" as c;

.product-history_content{
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px;
    gap: 5px;
    overflow-y: scroll;
    height: 100%;
}

.product-card_container{
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 7fr;
    justify-content: flex-start;
    border: 1px solid c.$grey_light;
    padding: 7px 5px;
    border-radius: 5px;
    height: auto;
    align-items: center;
    gap: 10px;

    img{
        height: 100px;
        width: auto;
    }

    section{
        display: flex;
        flex-direction: column;
        gap: 5px;

        p{
            color: c.$grey_dark;
            font-size: 14px;
        }
        h1{
            font-weight: 400;
            font-size: 18px;
        }
        a{
            font-size: 15px;
            font-weight: 500;
            color: c.$secundary;
        }
    }
}

@media (max-width: 400px) {
    .product-card_container{
        display: flex;
        padding: 5px;
        gap: 10px;
    
        img{
            height: 80px;
        }
    }
}